<template>
  <div>
    <Titletop title="个人中心">
      <router-link slot="l" replace :to="{name:'persion'}"></router-link>
      <router-link slot="r" replace to="/index"></router-link>
    </Titletop>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
export default {
name:'Mymineself',
components:{ Titletop},
mounted(){
    this.axios
      .get("https://jyapi.jyacg.com/admin/user/moneyinfo", {
        headers: {
          authorization: localStorage.getItem("api_token"),
        },
      })
      .then((res) => {
        if (res.status == 200) {
          if (res.data.code == 0) {
            localStorage.setItem("userapp", JSON.stringify(res.data.data));
          } else if (res.data.code == 1002) {
            if (res.data.msg == "账号验证失效，请重新登录") {
              localStorage.removeItem("api_token");
              this.$dialog({
                title: "温馨提示",
                message: res.data.msg,
              }).then(() => {
                this.$router.replace({
                  name: "login",
                });
              });
            }
          }
        }
      });
}
}
</script>

